// app/components/MovieBanner.tsx
import { Movie } from '@/types';

interface MovieBannerProps {
  movie: Movie;
}

// app/components/MovieBanner.tsx
export default function MovieBanner({ movie }: MovieBannerProps) {
  return (
    <div className="relative h-[95vh] w-full">
      <div 
        style={{
          backgroundImage: `url(https://image.tmdb.org/t/p/original${movie.backdrop_path})`,
          backgroundSize: 'cover',
          backgroundPosition: '50% 35%'
        }}
        className="absolute inset-0"
      >
        {/* Gradient overlay - matching reference exactly */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/40 to-transparent">
          <div className="absolute bottom-[15%] left-[5%] max-w-2xl">
            <h1 className="text-[4rem] font-bold text-white leading-tight mb-4">
              {movie.title}
            </h1>
            <p className="text-lg text-gray-200 mb-6 line-clamp-3">
              {movie.overview}
            </p>
            <div className="flex items-center gap-4">
              <div className="bg-yellow-400 text-black px-4 py-1.5 rounded-full font-semibold">
                IMDB: {movie.vote_average.toFixed(1)}
              </div>
              <div className="bg-white/20 backdrop-blur-sm text-white px-4 py-1.5 rounded-full">
                Released On: Jun 28
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}